import React from 'react';

function Register() {
  return (
    <div className="App">
    <form action="/action_page.php">
    <div class="container">
    <h1>Registration Form</h1>
      <label for="uname"><strong>Username</strong></label>
      <input type="text" placeholder="FullName" name="fname" required/>
      <label for="phone"><strong>Mobile Number</strong></label>
      <input type="tel" placeholder="1234567890" name="phone" pattern="[0-9]{3}[0-9]{3}[0-9]{4}" required/>
      <label for="email"><strong>Alternate-Email</strong></label>
      <input type="email" placeholder="example@gmail.com" name="aemail" required/>
      <label for="birthday"><strong>Date Of Birth</strong></label>
      <input type="date" placeholder="01-01-1999" name="birthday" required/>
      <label for="vurl"><strong>URL Of Video</strong></label>
      <input type="url" placeholder="URL"name="vurl" required/><br></br>
      <input type="submit" value="Submit" className="btn-btn"/>
    </div>
    
  </form>
  </div>
  );
}

export default Register;